import React from "react"
import PostListItem from "./PostListItem";

const PostsList = ({items}) => {
  return (
    <div>
    {
      items.map(({ node }) => (
        <PostListItem 
          item={node} 
          key={node.fields.slug}
        />
      ))
    }
    </div>
  );
};

export default PostsList;