import React from "react"
import { Link } from "gatsby";
import styles from "./PostListItem.module.scss";


const PostListItem = ({item}) => {
  const title = item.frontmatter.title || item.fields.slug;
  const getDetails = () => {
    const readTime = item.frontmatter.time 
                      ? `Â· ${item.frontmatter.time}` 
                      : "";
                      console.log()
    return  `${item.frontmatter.date} ${readTime}` 
  };

  const getCoverImage = () => {
   return  item.frontmatter.coverImage 
          ? item.frontmatter.coverImage.publicURL 
          : null;
  };

  return (
    <article className={styles.PostListItem+' row'}>
      <div className="col-sm-9">
        <header>
          <h3 className={styles.PostListItem__title}>
            <Link  to={item.fields.slug}>
              {title}
            </Link>
          </h3>
        </header>
        <section>
          <p
            className={styles.PostListItem__description}
            dangerouslySetInnerHTML={{
              __html: item.frontmatter.description || item.excerpt,
            }}
          />
          <small>{getDetails()}</small>
        </section>
      </div>
      <div className="col-sm-3">
        <img 
          className={styles.PostListItem__imageCover}
          src={getCoverImage()}
        />
      </div>
  </article>
  );
};

export default PostListItem;